.wrapper{
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: 60px 1fr;
    grid-template-areas: "aside header"
                        "aside main";
    min-height: 100vh;
}
.aside{
    grid-area: aside;
}
.header{
    grid-area: header;
}
.main{
    grid-area: main;
}
