.thumbnail{
    width: 50px;
    height: 35px;
}
.thumbnail__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.thumbnail__custom{
    background-color: #e5e5e5;
    border: 1px solid #b9b9b9;
    color: #484848;
    font-size: 9px;
    display: flex;
    justify-content: center;
    align-items: center;
}
